import React from "react";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Server from "./Server";
import ServerCard from "./ServerCard";
import "./app.css";
import AddServerOverlay from "./AddServerOverlay/AddServerOverlay";
import {
  loadServers,
  loadUserServers,
  purgeServers,
  setActiveServer,
} from "../../store/servers";
import { socketConnect, socketDisconnect } from "../../store/session";
import { openInNewTab, plusSvg, splashSvg } from "../utils";
function Application() {
  const user = useSelector((state) => state.session.user);
  const activeServer = useSelector((state) => state.servers.activeServer);
  const activeChannel = useSelector((state) => state.servers.activeChannel);
  const socket = useSelector((state) => state.session.socket);
  const servers = useSelector((state) => state.servers.servers);
  const [addServerOverlayed, setAddServerOverlayed] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    if (user) {
      dispatch(loadUserServers()).then((tempServers) => {
        if (!tempServers) return;
        if (!activeServer) {
          dispatch(setActiveServer(Object.values(tempServers)[0]));
        } else if (Object.keys(tempServers).includes(String(activeServer.id))) {
          dispatch(setActiveServer(tempServers[activeServer.id]));
        } else dispatch(setActiveServer(Object.values(tempServers)[0]));
      });
      dispatch(socketConnect());
    }
    return () => {
      dispatch(purgeServers());
      socketDisconnect();
    };
  }, []);

  useEffect(() => {
    // setTimeout(() => {
    if (Object.keys(servers).length == 0) return;
    if (!Object.keys(servers).includes(String(activeServer?.id))) {
      dispatch(setActiveServer(Object.values(servers)[0]));
    } else if (activeServer != servers[String(activeServer.id)]) {
      dispatch(setActiveServer(servers[String(activeServer.id)]));
    }
  }, [servers]);
  // useEffect(() => {}, [servers, activeServer]);
  if (Object.values(servers).length == 0)
    return (
      <>
        <div
          style={{
            height: "100vh",
            width: "100vw",
            position: "absolute",
            zIndex: "0",
            objectFit: "fill",
            background: "#5865f2",
          }}
        >
          {splashSvg()}
        </div>
        <AddServerOverlay
          setOverlay={setAddServerOverlayed}
          overlayed={addServerOverlayed}
        />
      </>
    );
  // if (activeServer == null) return <></>;
  return (
    <div className="app-container">
      {addServerOverlayed && (
        <AddServerOverlay
          setOverlay={setAddServerOverlayed}
          overlayed={addServerOverlayed}
        />
      )}
      <div className="server-browser">
        <div>
          {Object.keys(servers) != 0 &&
            Object.values(servers).map((server) => (
              <ServerCard key={server.id} server={server} />
            ))}
          <div
            className="pointer server-bubble add-server-bubble"
            onMouseDown={() => setAddServerOverlayed(true)}
          >
            {plusSvg()}
          </div>
        </div>
        <div>
          <div
            className="pointer server-bubble-container server-bubble linkedin-bubble"
            onClick={() => openInNewTab("https://www.linkedin.com/in/camer0n/")}
          >
            {
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
              >
                <path
                  fill="currentColor"
                  d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z"
                />
              </svg>
            }
          </div>
          <div
            className="server-bubble-container pointer server-bubble github-bubble"
            onClick={() => openInNewTab("https://github.com/cra88y/unicord/")}
          >
            {
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
              >
                <path
                  fill="currentColor"
                  d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"
                />
              </svg>
            }
          </div>
        </div>
      </div>

      {activeServer && <Server server={activeServer} />}
    </div>
  );
}

export default Application;
